      -webkit-transition: 200ms;
      -moz-transition: 200ms;
      -o-transition: 200ms;
      transition: 200ms; }
      .bg--green.hover-l:hover {
        background-color: #94c45b; }
  .bg--grey {
    background-color: #9B9B9B; }
    .bg--grey.hover-d {
      -webkit-transition: 200ms;
      -moz-transition: 200ms;
      -o-transition: 200ms;
      transition: 200ms; }
      .bg--grey.hover-d:hover {
        background-color: #8e8e8e; }
    .bg--grey.hover-l {
      -webkit-transition: 200ms;
      -moz-transition: 200ms;
      -o-transition: 200ms;
      transition: 200ms; }
