/*----------------------------------*    Links
\*----------------------------------*/
.link--normal:hover {
  text-decoration: underline; }

.link--mark {
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms; }
  .link--mark:hover {
    background: linear-gradient(to top, transparent 0%, transparent 20%, #80B541 21%, #80B541 50%, transparent 51%, transparent 100%); }

.link--arrow {
  position: relative;
  padding-right: 35px;
  font-weight: 600;
  font-family: "Hind";
  font-size: 16px; }
  .link--arrow:after {
    content: url(../images/ArrowGreen.svg);
    position: absolute;
    right: 5px;
    -webkit-transition: 200ms;
    -moz-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms; }
  .link--arrow:hover:after {
    right: 0; }

.link--light {
  color: #B6BF8C;
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms; }
  .link--light:hover {
    color: #80B541; }

